<template>
  <div>
    <div class="dd-cx">
      <a-button class="dd-cx_bt" type="primary">订单查询</a-button>
      <a-date-picker placeholder="选择查询日期" @change="handleDate">
        <template slot="dateRender" slot-scope="current, today">
          <div class="ant-calendar-date" :style="getCurrentStyle(current, today)">{{current.date()}}</div>
        </template>
      </a-date-picker>
    </div>
    <a-table :columns="columns" :dataSource="data" v-if="data">
      <a slot="zfbname" slot-scope="zfbname" href="javascript:;">{{zfbname}}</a>
      <span slot="customTitle">
        <a-icon type="smile-o"/>会员支付宝
      </span>
      <span slot="StautsType" slot-scope="StautsType, record, index">
        <a-tag :color="tipcolor(index)" @click="handleInputChange(index)">{{StautsType}}</a-tag>
      </span>
      <a
        class="money"
        slot="CommissionTrans"
        slot-scope="CommissionTrans, record, index"
        @click="jump(record)"
      >{{CommissionTrans}}元</a>
    </a-table>
  </div>
</template>
<script>
import {
  getCashDrawlList,
  upCashDrawlList,
  searchCashDrawList
} from "@/api/cashdrawal";
import { mapGetters } from "vuex";
const columns = [
  {
    dataIndex: "zfbname",
    key: "zfbname",
    slots: { title: "customTitle" },
    scopedSlots: { customRender: "zfbname" }
  },
  {
    title: "支付宝账号",
    dataIndex: "zfb",
    key: "zfb"
  },
  {
    title: "提现时间",
    dataIndex: "TransDate",
    key: "TransDate"
  },
  {
    title: "提现金额",
    dataIndex: "CommissionTrans",
    key: "CommissionTrans",
    scopedSlots: { customRender: "CommissionTrans" }
  },
  {
    title: "审核状态",
    key: "StautsType",
    dataIndex: "StautsType",
    scopedSlots: { customRender: "StautsType" }
  },
  {
    title: "合伙人编号",
    dataIndex: "usercode",
    key: "usercode"
  }
];

export default {
  data() {
    return {
      data: [],
      columns
    };
  },
  computed: {
    ...mapGetters(["txmessage"])
  },
  created() {
    let { type } = this.$route.query;
    this.init(type);
  },
  methods: {
    init(type) {
      getCashDrawlList().then(res => {
        let data = res.data;
        this.data = [];
        if (data.code != 200) return this.$message.error("请求出错！");
        data = data.data;
        data.forEach(element => {
          if (element.StautsType == "已审核" && type == "1") {
            this.data.push(element);
          } else if (element.StautsType == "审核中" && type == "0") {
            this.data.push(element);
          }
        });
      });
    },
    handleDate(montent, date) {
      let {type} = this.$route.query
      console.log('日期',date)
      searchCashDrawList({ date }).then(res => {
        console.log('查询的数据',res)
        let data = res.data;
        this.data = [];
        if (data.code != 200) return this.$message.error("请求出错！");
        data = data.data;
        data.forEach(element => {
          if (element.StautsType == "已审核" && type == "1") {
            this.data.push(element);
          } else if (element.StautsType == "审核中" && type == "0") {
            this.data.push(element);
          }
        });
      });
    },
    jump(params) {
      this.$router.push({ path: "/orderlist", query: { pid: params.PID } });
      console.log(params);
    },
    getCurrentStyle(current, today) {
      const style = {};
      if (current.date() === 1) {
        style.border = "1px solid #1890ff";
        style.borderRadius = "50%";
      }
      return style;
    },
    handleInputChange(index) {
      // this.data[index].StautsType = "已审核";
      this.data[index].StautsType = "已审核";
      let idx = this.indexOf(this.data[index]);
      let t = this.txmessage;
      if (idx != -1) {
        t.splice(idx, 1);

        this.$store.commit("SET_TXMESSAGE", t);
      }
      upCashDrawlList({ id: this.data[index].ID }).then(res => {
        console.log(res);
      });
    },
    indexOf(item) {
      let idx;
      console.log("选择的item", item);
      for (let i in this.txmessage) {
        if (item.ID == this.txmessage[i].ID) {
          idx = i;
          break;
        }
      }
      if (idx) return idx;
      return -1;
    },
    tipcolor(index) {
      let color;
      return (color =
        this.data[index].StautsType == "已审核" ? "green" : "blue");
    }
  },
  watch: {
    $route(to, from) {
      let { type } = this.$route.query;
      this.init(type);
    }
  }
};
</script>
<style lang="less" scoped>
.money {
  cursor: pointer;
}
.dd-cx {
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
.dd-cx_bt {
  margin-right: 10px;
}
</style>



